import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { Router } from "@angular/router";
import { CFSummary } from "../../interfaces/query-params.interface";
import { PatientAdmittedModalComponent } from "src/app/active-patients/patient-admitted-modal/patient-admitted-modal.component";
import { MatDialog } from "@angular/material/dialog";
import { AdmitPatientModalComponent } from "src/app/active-patients/admit-patient-modal/admit-patient-modal.component";
import { LSTableConfig, TableState } from "src/app/life-signals/_models/ls-column.model";
import { ActivePatientManagerService } from "src/app/active-patients/manager/active-patient-manager.service";
import { ActivePatientsService } from "src/app/active-patients/services/active-patients.service";

@Component({
  selector: "app-setting-card",
  templateUrl: "./setting-card.component.html",
  styleUrls: ["./setting-card.component.scss"],
})
export class SettingCardComponent implements OnInit {
  constructor(private router: Router,
    private dialog: MatDialog, 
    private manager: ActivePatientManagerService,
    private service: ActivePatientsService,
    ) {
      // console.log(service);
    }
  @Output() fetchSummary: EventEmitter<any> = new EventEmitter();
  @Input() title: any;
  @Input() image: any;
  @Input() count: any;
  @Input() user: any;
  @Input() timerIcon: any;
  @Input() routerUrl: any;
  @Input() disabled: any;
  @Input() subTitle: any;
  @Input() view: boolean = true;
  @Input() showCount: boolean = true;
  @Input() loading: boolean = false;
  @Input() type: any;
  @Input() register?: boolean;
  selectedTimeParam = null;
  timerList = [
    {
      time: "Current",
      value: 0,
    },
    {
      time: "30 mins",
      value: 30,
    },
    {
      time: "1 hour",
      value: 60,
    },
    {
      time: "3 hours",
      value: 3*60,
    },
    {
      time: "6 hours",
      value: 6*60,
    },
        // {
    //   time: "1 Day",
    //value: 24*60,
    // },
    //    //   time: "10 Days",
    //   value: 1000*24*60,
    // }
  ];

  ngOnInit(): void {
    if(this.type === 'parameter')
    this.selectedTimeParam = +window.sessionStorage.getItem("parameter-duration");
    else if (this.type === 'arrhythmia')
      this.selectedTimeParam = +window.sessionStorage.getItem("arrhythmia-duration");
    if (this.selectedTimeParam === undefined) {
      this.selectedTimeParam = 360;
    }
  }

  navigateUrl(url: string) {
    this.router.navigate([url]).then();
  }

  // Alert fetch
  changeDuration(event: any, type: string) {
    const param: CFSummary = {};
    switch (type) {
      case "parameter":
        param.parameterAlertsFrom = event.value;
        param.arrhythmiaAlertsFrom = window.sessionStorage.getItem("arrhythmia-duration");
        if(param.arrhythmiaAlertsFrom === undefined)
          param.arrhythmiaAlertsFrom = '360';
        this.selectedTimeParam = event.value;
        window.sessionStorage.setItem("parameter-duration", this.selectedTimeParam);
        break;
      case "arrhythmia":
        param.arrhythmiaAlertsFrom = event.value;
        param.parameterAlertsFrom = window.sessionStorage.getItem("parameter-duration");
        if(param.parameterAlertsFrom === undefined)
          param.parameterAlertsFrom = '360';
        this.selectedTimeParam = event.value;
        window.sessionStorage.setItem("arrhythmia-duration", this.selectedTimeParam);
        break;
      default:
        break;
    }
    this.fetchSummary.emit(param);
  }

  getTimeName(value){
    return this.timerList.find(t => t.value === value)?.time || "6h";
  }

  checkUrl(url : string){
    if(this.title && url && this.view){
      this.router.navigate([url]).then();
    }
  }

  // For Opening Registration Modal ////////////
  config: LSTableConfig = {
    id: "active-patients-table",
    rowSelectEnabled: true,
    actions: {
      show: true,
      headerLabel: "",
      class: ["d-flex", "align-items-center", "justify-content-end"],
      header: {
        style: "max-width: 180px; width: 180px;",
      },
    },
    page: {
      pageSize: 2,
      pageIndex: 0,
      length: null
    },
    paginator: {
      offline: false,
      hide: false
    },
    sort: {
      active: "first_name",
      direction: "desc"
    },
    tableState: TableState.DEFAULT
  };

  searchObject: { keyword: string; string: string } = {
    keyword: "",
    string: "",
  };
  filterObject: { cGroupId: string; pGroupId: string ;doctor:string } = {
    cGroupId: null,
    pGroupId: null,
    doctor: null
  };

  getActivePatientData() {
    if (!this.config.sort.active || !this.config.sort.direction) {
      this.config.sort.active = "first_name";
      this.config.sort.direction = "desc";
    }
    this.manager.getActivePatient(this.service.actvePatientsUrl(this.config, this.searchObject, this.filterObject));
  }

  admitPatientModal(data?: any) {
    const dailogRef = this.dialog.open(AdmitPatientModalComponent, {
      maxWidth: "96vw",
      minWidth: "320px",
      width: "1200px",
      height:"900px",
      backdropClass: "backdropBackground",
      disableClose: true,
      data: { ...data },
    });
    return dailogRef.afterClosed();
  }

  openRegistrationModal(){
    this.admitPatientModal().subscribe(resp => {
      if (resp) {
        this.getActivePatientData();
        this.dialog.open(PatientAdmittedModalComponent, {
          maxWidth: "96vw",
          width: "520px",
          backdropClass: "backdropBackground",
          disableClose: true,
          autoFocus: false,
          data: { title: 'active-patients-module.dialog.patient_admitted' }
        });
      }
    });
  }
  /////////////////
}
